import React, { Component } from "react";
import { connect } from "react-redux";
import { ThemeStyle } from "../utils/style"
import {
    onChangeWebSocketConnectState,
    setWebSocket,
    showConnectLoading,
} from "../actions/message"
import {
    initSessionListData,
    addAllUserInfoData,
    addMoreAllUserInfoData,
    addMessageListViewData,
} from "../actions/message/sessionList"
import {
    changeMessageItemData,
    addMessageItemData,
} from "../actions/message/messageSend"
import {
    userLogin
} from "../actions/user"
import ExceptionConnectState from "../pages/ExceptionConnectState"
import MessagePanel from "../pages/MessagePanel"
import { Toast } from 'antd-mobile';

class Container extends Component {
    componentWillReceiveProps(prevProps){

        const oldIsShowConnectLoading = this.props.isShowConnectLoading

        const {
            isShowConnectLoading,
            connectNumber,
        } = prevProps

        if(oldIsShowConnectLoading===false && isShowConnectLoading===true){
            Toast.loading('正在重新连接...',0)
        }
        if(oldIsShowConnectLoading===true && isShowConnectLoading===false){
            Toast.hide()
        }
        if(connectNumber>=3 && isShowConnectLoading===false){
            Toast.hide()
        }

    }
    componentDidMount(){

    }
    componentDidMount(){

        const {
            onChangeWebSocketConnectState,
            setWebSocket,
            userLogin,
            initSessionListData,
            allUserInfoData,
            addAllUserInfoData,
            addMoreAllUserInfoData,
            addMessageListViewData,
            allMessageListData,
            changeMessageItemData,
            addMessageItemData,
            access_token,
        } = this.props




        const socket = new WebSocket('ws://ws.pinggai.cc');

        socket.onopen = (event)=>{

            setWebSocket({
                socket
            })

            socket.send(JSON.stringify({
                type: 'login',
                data: {
                    access_token,
                }
            }))

        }

        socket.onmessage = (e)=>{

            const data = JSON.parse(e.data)

            switch (data.type) {
                case 'user.self':
                    userLogin({
                        userInfoData: data.data.user_info
                    })
                    onChangeWebSocketConnectState({
                        state : 1
                    })
                    break;
                case 'login':
                    if(data.code===0){
                        socket.send(JSON.stringify({
                        	type: 'user.self',
                        }))
                        socket.send(JSON.stringify({
                            type: 'message.session.list',
                        }))
                    }else {
                        Toast.info(data.msg)
                    }
                    break;
                case 'message.session.list':
                    const {
                        session_list
                    } = data.data
                    initSessionListData({list:session_list})
                    let newArray = []
                    session_list.map((item)=>{
                        if(!allUserInfoData[item.relation_id]){
                            newArray.push(item.relation_id)
                        }
                    })
                    if(newArray.length){
                        socket.send(JSON.stringify({
                            type: 'user.infos',
                            data: {
                                user_ids: newArray,
                            }
                        }))
                    }
                    break;
                case 'user.info':
                    if(data.code===0){
                        const {
                            user_info
                        } = data.data
                        addAllUserInfoData({
                            id: user_info.id,
                            data: user_info
                        })
                    }else {
                        Toast.info(data.msg)
                    }
                    break;
                case 'user.infos':
                    if(data.code===0){
                        const {
                            user_list
                        } = data.data
                        let newData = {}
                        user_list.map((item)=>{
                            newData[item.id] = item
                        })
                        addMoreAllUserInfoData({
                            data: newData
                        })
                    }else {
                        Toast.info(data.msg)
                    }
                    break;
                case 'message.list':
                    if(data.code===0){
                        const {
                            relation_id
                        } = data.data
                        addMessageListViewData({
                            id: relation_id,
                            data: data.data
                        })
                    }else {
                        Toast.info(data.msg)
                    }
                    break;
                case 'message':
                    if(data.code===0){
                        const {
                            sign,
                            relation_id,
                            user_id,
                        } = data.data
                        const {
                            allMessageListData,
                            sessionListData,
                            userInfo,
                            listViewInstance,
                        } = this.props

                        const relationId = userInfo.id===relation_id?user_id:relation_id

                        const sessionListIndex = sessionListData.findIndex((e)=>{return e.relation_id===relationId})

                        if(sessionListIndex !== -1){

                            if(allMessageListData[relationId]){
                                const index = allMessageListData[relationId].list.findIndex((item)=>{
                                    return item.sign===sign
                                })

                                if(index!==-1){
                                    changeMessageItemData({
                                        id: relationId,
                                        data: data.data,
                                        index,
                                        allMessageListData
                                    })
                                }else {
                                    addMessageItemData({
                                        id: relationId,
                                        data: data.data,
                                        allMessageListData
                                    })
                                }

                                setTimeout(()=>{
                                    listViewInstance&&listViewInstance()
                                },100)

                            }else {
                                socket.send(JSON.stringify({
                                    type: 'message.list',
                                    data: {
                                        type: 'user',
                                        relation_id:relationId,
                                        page: 1,
                                        rows: 10,
                                    }
                                }))
                            }
                        }else {
                            Toast.info('另外一个新客服的消息，未开发')
                        }
                    }else {
                        Toast.info(data.msg)
                    }
                    break;
                default:
                    return false
            }
        }

        socket.onerror = (event)=>{
            onChangeWebSocketConnectState({
                state : 3
            })
        }

        socket.onclose = (e)=>{
            const {
                connectNumber
            } = this.props

            if(connectNumber<=3){

                showConnectLoading(true,connectNumber+1)
                this.componentDidMount()

            }else {

                showConnectLoading(false,connectNumber)
                onChangeWebSocketConnectState({
                    state : 2
                })

            }
        }


    }
    render() {
        const {
            connectState,
            showPanelModal,
        } = this.props

        switch (connectState) {
            case 0:
                return <ExceptionConnectState errorInfo={'loading'} visible={showPanelModal}/>
            case 1:
                return <MessagePanel {...this.props}/>
            case 2:
                return <ExceptionConnectState errorInfo={'连接正在关闭'} visible={showPanelModal}/>
            case 3:
                return <ExceptionConnectState errorInfo={'连接已经关闭，或者连接无法建立'} visible={showPanelModal}/>
            default:
                return <ExceptionConnectState errorInfo={'connectState异常'} visible={showPanelModal}/>
        }
    }
    firstConnectOver(e){
        const {
            initCallBack
        } = this.props
        initCallBack&&initCallBack(e)
    }
}

const mapStateToProps = ({app,auth,view}) => {
    const {
        user,
    } = app
    const {
        userInfo
    } = user
    const {
        message
    } = view
    const {
        connectState,
        showConnectLoading,
        allUserInfoData,
        allMessageListData,
        listViewInstance,
        sessionListData,
        showPanelModal,
        connectNumber,
    } = message
    return {
        connectState,
        isShowConnectLoading: showConnectLoading,
        allUserInfoData,
        allMessageListData,
        listViewInstance,
        userInfo,
        sessionListData,
        showPanelModal,
        connectNumber,
    }
}

export default connect(mapStateToProps,
    {
        onChangeWebSocketConnectState,
        setWebSocket,
        userLogin,
        showConnectLoading,
        initSessionListData,
        addAllUserInfoData,
        addMoreAllUserInfoData,
        addMessageListViewData,
        changeMessageItemData,
        addMessageItemData,
    }
)(Container)



